.event{
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.event h1{
    font-size: 3rem;
}

.eventcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.e-card{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.25);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}

.e-image{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.e-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.e-image:hover img{
    transform: scale(1.3);
}

.e-card h4{
    font-size: 1.3rem;
    padding: 0.9rem 0 .2rem 0;
}


@media screen and (max-width: 850px) {
    .event{
        margin:4rem 2rem;
    }

    .eventcard{
        flex-direction: column;
    }

    .e-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}


