.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}

img{
    width: 100%;
    height: 100%;
    object-fit: cover; 
}

.hero-text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}

.hero-mid h1{
    padding-top: 4rem !important;
}


.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-text p{
    font-size: 1.3rem;
    color: white;
    padding: 0.5rem 0 2rem 0;
}


.hero-text .show{

    text-decoration: none;
    background-color: var(--fourth-color);
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: var(--first-color);
    transition:all 0.3s ease-in-out;
}

.hero-text .hide{
    display: none;
}

.show:hover{
    background-color: var(--second-color);
    color:var(--fourth-color);
    transition:all 0.3s ease-in-out;
}

@media screen and (max-width: 555px) {
    .hero{
        width: 100%;
        height: 94vh;
        position: relative;
    }
    
    .hero-text h1{
        padding: 10px 0px;
    }

    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }

    .hero-text .show{
       
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
        

    }
}